.observation-row {
  border: 1px solid #b7b7b7;
  box-shadow: 0 0 2px #b7b7b7;
  background: #FFFFFF;
  padding: 25px 10px 10px;
  margin: 10px 5px;
  border-radius: 3px;
  position: relative;
}
.observation-row span {
}

.observation-name {
  font-size: 20px;
}
.observation-scientificname {
  font-size: 14px;
  font-weight: 400;
}
.observation-time {
  position: absolute;
  left: 10px;
  top: 5px;
  font-size: 14px;
  font-weight: 400;
}

.observation-rarity {
  position: absolute;
  right: 10px;
  top: 5px;
  border: 1px solid #b2b2b2;
  border-radius: 3px;
  padding: 2px;
  text-align: right;
}

.observation-note {
  font-size: 14px;
  color: #777777;
}

.observation-rarity--rare {
  background-color: orange;
}

.observation-rarity--extremely-rare {
  background-color: tomato;
}

.observation-rarity--common {
  background-color: greenyellow;
}
